import { QRCodeSVG } from "qrcode.react";
import { useState } from "react";
import { i18n, constants } from "./resources";
import { WrapperContainer, QRCodeWrapperPaper } from "./styles";
import { TextField } from "@mui/material";

export const QRCode = () => {
  const [value, setValue] = useState("");

  const handleOnChange: React.ChangeEventHandler<
    HTMLInputElement | HTMLTextAreaElement
  > = (event) => {
    setValue(event.target.value);
  };

  return (
    <WrapperContainer maxWidth="sm">
      <QRCodeWrapperPaper elevation={2}>
        <QRCodeSVG
          value={value}
          title={i18n.qrCodeTitle}
          size={constants.qrCodeSize}
          level="L"
          marginSize={0}
        />
      </QRCodeWrapperPaper>
      <TextField
        id="outlined-multiline-static"
        label={i18n.inputLabel}
        multiline
        rows={4}
        placeholder={i18n.placeholder}
        onChange={handleOnChange}
        fullWidth
      />
    </WrapperContainer>
  );
};
